.img-slide {
    &__photo {
        display: flex;
        align-items: center;
        justify-content: center;
        img {
            display: block;
            max-width: 100%; } }
    &__bottom {
        // position: relative
        z-index: 100;
        padding: 13px 0;
        // top: 100%
        // left: 0
        width: 100%;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        flex-wrap: wrap;
        color: $white;
        font-size: 13px;
        font-weight: 500;
        font-family: $avenir;
        text-align: left; }
    &__name {
        font-weight: 700; }
    &__link {
        display: block;
        color: $white;
        font-size: 13px; }
    &__left {
        max-width: 550px; }
    &__right {
        display: flex; }
    &__ref {
        position: relative;
        &:not(:first-child) {
            padding-left: 33px;
            &:before {
                content: '';
                position: absolute;
                height: 14px;
                width: 1px;
                top: 50%;
                left: 16px;
                transform: translateY(-50%);
                background-color: rgba(#D8D8D8,.4); } } } }
