.list-section {
  &:not(:last-child) {
    margin-bottom: 23px; }
  &.is-loading {
    flex-grow: 1;
    background-image: linear-gradient(to bottom, $white 0%, $white 50%, transparent 50%), linear-gradient(-48deg, #fefefe 28%, #f0f0f0 100%);
    background-size: 100% 40px, cover;
    @include md {
      min-height: 40vh; } }
  &__title {
    margin-bottom: 12px;
    color: rgba($gray, 0.6);
    @include f(18px, 500, 1.3);
    @include sm {
      margin-bottom: 6px; } } }

.accordion {
  &_mob-margin {
    @include sm {
      margin: 0 -20px; } }
  &:not(:last-child) {
    margin-bottom: 4px; }
  &.is-open {}
  &__trigger {
    display: block; }
  &__content {
    // display: none
    overflow: auto;
    position: relative;
    width: 100%; }
  &__photos {
    display: none; } }

.list-head {
  display: flex;
  align-items: center;
  padding: 12px;
  height: 48px;
  background-color: rgba(#b4c3ec, 0.11);
  transition: background-color .3s;
  @include hover {
    background-color: rgba(#b4c3ec, 0.3); }
  &__plus {
    display: block;
    margin-right: 12px;
    @include circle(20);
    background-color: rgba(#ebedf2, 0.84);
    transition: background-color .3s;
    position: relative;
    cursor: pointer;
    .is-open & {
      @include sm {
        background-color: transparent; } }
    &:before,
    &:after {
      content: '';
      display: inline-block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scaleY(1);
      background-color: $gray; }
    &:before {
      height: 1px;
      width: 8px; }
    &:after {
      height: 8px;
      width: 1px;
      transition: transform 0.5s;
      transform-origin: 50% 50%;
      .is-open & {
        transform: translate(-50%, -50%) scaleY(0); } } }
  &__title,
  &__counter {
    @include f(18px, 500, 1.2);
    display: inline-block;
    vertical-align: middle; }
  &__title {
    color: $gray; }
  &__counter {
    color: #a6a6a6;
    margin-left: 5px; } }

.list-table {
  min-width: 560px;
  padding-bottom: 12px;
  @include md {
    min-width: 940px; }
  &__tr {
    position: relative;
    padding: 16px 32px 15px 38px;
    text-align: left;
    font-size: 0;
    transition: background-color $tr;
    cursor: pointer;
    font-family: $avenir;
    display: flex;
    @include lg {
      padding: 16px 15px 15px; }
    @include md {
      padding: 16px 32px 15px 38px; }
    @include hover {
      background-color: #dae2ff;
      & > * {
        font-weight: 600; }
      .list-table__eye {
        @include fadein; } }
    &:not(:last-child) {
      border-bottom: 1px solid #979797; }
    & > * {
      padding: 0 6px;
      color: $black;
      @include f(14px, 400, 1.214); }
    &_head {
      padding: 18px 32px 12px 38px;
      & > * {
        font-weight: 500; } } }
  &__counter {
    width: 73px; }
  &__soporte {
    width: 186px; }
  &__direccion {
    width: 198px; }
  &__comienza {
    width: 158px;
    font-size: 13px;
    word-break: break-all; }
  &__finaliza {
    width: 151px;
    font-size: 13px;
    word-break: break-all; }
  &__ots {
    width: 90px;
    text-align: center;
    font-size: 13px;
    @include lg {
      width: 110px; }
    @include md {
      width: 90px; } }
  &__eye {
    position: absolute;
    top: 50%;
    right: 10px;
    margin-top: -7px;
    @include fadeout($tr);
    .icon {
      font-size: 14px;
      color: $blue; } } }

.list-sort-btn {
  background-color: transparent;
  font-family: $avenir;
  &.is-ACS:after {
    transform: rotate(180deg); }
  span {
    color: $black;
    @include f(14px, 500, 1.214); }
  &:after {
    content: '';
    display: inline-block;
    vertical-align: middle;
    @include arr(7, 5, $blue, b);
    margin-top: -2px;
    transition: transform $tr; } }
