.tabs {
	&__header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-wrap: wrap;
		padding: 9px 11px 9px 17px;
		margin-bottom: 12px;
		background-color: rgba($blue, 0.04);
		@include sm {
			padding: 0;
			margin-bottom: 0;
			background-color: transparent; } }
	&__mobile-header {
		@include sm {
			@include fls(100%);
			display: flex;
			justify-content: space-between;
			align-items: center;
			position: relative;
			padding: 12px 20px 12px 34px;
			z-index: 20;
			cursor: pointer;
			background-color: rgba($blue, 0.04);
			&:before,
			&:after {
				content: '';
				position: absolute;
				top: 100%;
				height: 48px;
				width: 30px; }
			&:before {
				right: 0;
				background-image: linear-gradient(to left, $white, rgba($white, 0.24)); }
			&:after {
				left: 0;
				background-image: linear-gradient(to right, $white, rgba($white, 0.24)); } }
		.mob-accordion-btn {
			display: none;
			@include sm {
				display: block; } } }
	&__title-wrapper {
		color: $blue;
		font-size: 0;
		& > * {
			display: inline-block;
			vertical-align: middle; } }
	&__title-icon {
			margin-top: -4px;
			margin-right: 11px;
			@include sm {
				margin-top: 0; }
			.icon {
				font-size: 23px; } }
	&__title {
		font-family: $avenir;
		@include f(25px, 500, 1.24);
		font-size: 25px;
		font-weight: 500;
		@include sm {
			@include f(20px, 500, 1.25); } }
	&__controls {
		position: relative;
		@include sm {
			padding: 14px 0 24px 33px;
			overflow: hidden; }
		.tabs-list {
			@include sm {
				position: relative;
				z-index: 1; } } }
	&__content {
		position: relative;
		line-height: 2; }
	&__item {
		padding: 10px 0; }
	&__mob-buttons {
		display: none;
		@include sm {
			display: block; } } }


.tabs-list {
	display: flex;
	align-items: center;
	justify-content: center;
	@include sm {
		justify-content: flex-start; }
	&__item {
		position: relative;
		padding: 0 18px 0 19px;
		line-height: 1.3077;
		@include sm {
			flex-shrink: 0;
			@include sm {
				padding: 0 32px 0 0;
				border-bottom: 1px solid transparent; } }
		&:not(:last-child) {
			border-right: 1px solid rgba(151, 151, 151, 0.51);
			@include sm {
				border-right: none; } } }
	&__btn {
		display: inline-block;
		background-color: transparent;
		color: $gray;
		font-family: $avenir;
		@include f(13px, 500, 1.3077);
		transition: color $tr;
		@include sm {
			border-bottom: 1px solid transparent;
			padding-bottom: 3px; }
		&.is-active {
			color: $blue;
			font-weight: 600;
			@include sm {
				border-bottom-color: $blue; } } } }

[data-tabs] {
	overflow: hidden;
	[data-tabs-control] {
		cursor: pointer;
		user-select: none;
		&[disabled],
		&.is-disabled {
			cursor: not-allowed;
			@include nosel; } }
	[data-tabs-wrapper] {
		position: relative; }
	[data-tabs-container] {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		opacity: 0;
		visibility: hidden;
		transition: opacity 0.3s, visibility 0.3s;
		&.is-open {
			position: relative;
			opacity: 1;
			visibility: visible; } } }
