.svg-map {
	position: relative;
	overflow: hidden;
	background-color: #dcdcdc;
	@include sm {
		margin: 0; }
	&__inner {
		position: relative;
		height: 334px;
		@include sm {
			height: 365px; }
		svg {
			path {
				cursor: pointer; } } }
	.zoomButton {
		display: none; } }
.mapTooltip {
	position: absolute;
	top: 43px !important;
	left: auto !important;
	right: 47px !important;
	width: 243px;
	max-height: 247px;
	overflow: auto;
	padding: 18px 20px;
	color: $gray;
	font-size: 13px;
	box-shadow: 0 9px 17px rgba($black, .09);
	border-radius: 5px;
	background-color: $white;
	@include sm {
		top: auto !important;
		right: 0 !important;
		bottom: 0;
		width: 100%;
		border-radius: 5px 5px 0 0; }
	&[style*="block"] {
		@include sm {
			display: grid !important;
			grid-template-columns: 1fr 1fr 1.5fr 1fr 0.5fr; } } }

.tooltip-title {
	color: $blue;
	@include f(20px,500,1.35);
	@include sm {
		padding-right: 5px;
		font-size: 15px;
		grid-column: 1;
		grid-row: 1 / 3; }
	&:not(:last-child) {
		margin-bottom: 7px; } }
.tooltip-label {
	color: rgba($gray, .7);
	line-height: 1.38;
	grid-area: 1;
	@include sm {
		padding: 0 0 5px 15px;
		font-size: 9px;
		height: 100%; } }
.tooltip-value {
	@include f(14px,600,1.25);
	@include sm {
		padding-left: 15px;
		width: 100%;
		font-size: 11px; }
	&:not(:last-child) {
		margin-bottom: 13px;
		@include sm {
			margin-bottom: 0; } } }
