.video-block {
    &__top {
        display: flex;
        @include sm {
            flex-wrap: wrap; } }
    &__bottom {
        margin-top: 26px;
        @include sm {
            padding: 0 21px; } }
    &__video-wrap {
        width: 55%;
        @include sm {
            width: 100%;
            padding: 0 29px; }
        .slick-list {
            @include sm {
                overflow: visible; } }
        .slick-slide {
            padding: 0 7px; } }
    &__video {
        height: 290px;
        display: flex;
        align-items: center;
        background-color: $black;
        @include sm {
            height: auto;
            position: relative;
            width: 100%;
            padding-bottom: 56%;
            overflow: hidden; }
        iframe,
        video {
            @include sm {
                position: absolute;
                top: 0;
                left: 0;
                object-fit: cover;
                object-position: center; } }
        iframe {
            width: 100% !important;
            height: 100% !important; } }
    &__slider {
 }        // height: 100%
    &__next,
    &__prev {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        background-color: transparent;
        z-index: 10;
        svg {
            display: block; } }
    &__next {
        right: 21px; }
    &__prev {
        left: 21px; } }
