.megamenu {
	// max-width: 934px
	&__list {
		@include sm {
			margin: 0 -10px; } }
	&__item {
		margin-bottom: 25px;
		@include sm {
			padding: 0 10px;
			margin-bottom: 19px; } } }

.item {
	position: relative;
	padding: 12px 33px 17px;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	border-radius: 7px;
	min-height: 223px;
	color: $white;
	font-size: 32px;
	height: 100%;
	background-image: linear-gradient(47deg, #A8BBD6 0%, #C7CFDB 100%);
	box-shadow: 0 3px 5px rgba($black, .16);
	transition: box-shadow .3s;
	@include hover {
		box-shadow: 0 9px 14px rgba($black, .23);
		&:before {
			opacity: 1; } }
	@include sm {
		justify-content: center;
		font-size: 17px;
		min-height: 107px;
		padding: 10px; }
	&:before {
		position: absolute;
		content: "";
		z-index: 1;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border-radius: 7px;
		opacity: 0;
		transition: opacity .3s;
		background-image: linear-gradient(206deg, #2782F8 0%, #114BEF 95%); }
	&_low {
		min-height: 147px; }
	&__content {
		position: relative;
		z-index: 2;
		width: 100%;
		display: flex;
		justify-content: space-between;
		@include sm {
			flex-direction: column;
			justify-content: center;
			align-items: center; } }
	&__name {
		align-self: flex-end;
		@include sm {
			align-self: auto;
			order: 3; } }
	&__icon {
		max-width: 100px;
		padding-bottom: 13px;
		@include sm {
			padding-bottom: 6px; }
		img {
			display: block;
			max-width: 100%; } } }

.head-links {
	&__list {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -14px;
		padding-top: 22px;
		@include sm {
			margin-bottom: -17px;
			padding-top: 12px; } }
	&__item {
		@include fls(25%);
		padding: 0 14px 12px;
		@include sm {
			padding: 0 14px 17px;
			@include fls(50%); } } }
